(function($) {

    // ===================== Experiment Model =================
    var TextAnalyzerModel = Backbone.Model.extend({
        url: url_api_textanalyzer
    });

    // ===================== User Model =======================
    var UserModel = Backbone.Model.extend({
        url: url_api_user_specific + get_experiment_id()
    });

    // ==================== Text Model Complete ===============
    var TextModelComplete = Backbone.Model.extend({
        
        url: url_api_textdata + get_experiment_id(),

        defaults: {
            "pre_text": null,
            "post_text": null,
            "pre_num_sentences": null,
            "post_num_sentences": null,
            "pre_num_clusters": null,
            "post_num_clusters": null,
            "pre_num_coherent_sentences": null,
            "post_num_coherent_sentences": null,
            "pre_num_non_coherent_sentences": null,
            "post_num_non_coherent_sentences": null,
            "pre_page_duration": null,
            "post_page_duration": null,
            "pre_num_concepts": null,
            "post_num_concepts": null
        }
    });

    // ================== My View =======================
    var MeasurementView = Backbone.View.extend({

        el: $('.measurement'),

        events: {
            "click #measurement_instruction-button": "renderExperiment",
            "click #measurement_measurement-input-button": "analyzeText",
            // "click #measurement_measurement-review-toggle-button": "toggleText",
            "click #measurement_measurement-review-send-button": "sendData",
            "keyup #measurement_measurement-review-text textarea": "reviseText",
        },

        initialize: function() {

            // Variable declaration
            var self = this;
            this.toggle_text = false;
            this.prePageDurationStart = null;
            this.postPageDurationStart = null;

            // Initialize templates
            this.instruction_template = _.template($('#measurement_measurement-instruction-template').html());
            this.input_template =  _.template($('#measurement_measurement-input-text-template').html());
            this.loading_template = _.template($('#loading-ring-template').html());
            this.review_template = _.template($('#measurement_measurement-review-template').html());
            this.text_save_error_template = _.template($('#measurement_error').html());

            // Initialize models
            this.user_model = new UserModel();
            this.text_complete_model = new TextModelComplete();
            this.analyzer = new TextAnalyzerModel();

            // Fetch user data
            this.user_model.fetch({
                success: function() {
                    self.renderInstruction();
                },
                error: function() {
                    console.log("error fetching user data");
                }
            });

        },

        renderInstruction: function() {
            // Render input div
            this.$el.html(this.instruction_template({'instruction': this.user_model.get('instruction')}));
        },

        renderExperiment: function() {

            // Start time
            this.prePageDurationStart = new Date();

            // Render input template
            this.$el.html(this.input_template());

            // Render divs according to windows height
            var window_height = $(window).height();

            // Input div
            var input_div = $('#measurement_measurement-input');

            // Render input div
            input_div.html(this.input_template());

            // Focus textarea
            var textarea = $('#measurement_measurement-input-textarea');
            textarea.focus();

            // Set heights for input elements
            $('#measurement_measurement-input').find("textarea").height(window_height * 0.6);

        },

        reviseText: function() {

            // Get textarea
            var textareaText = $('#measurement_measurement-review-text').find('textarea').val();

            // Set post-text
            this.text_complete_model.set({'post_text': textareaText});

        },

        analyzeText: function() {

            // Set duration for pre page
            var elapsed = (new Date() - this.prePageDurationStart) / 1000;
            this.text_complete_model.set({'pre_page_duration': elapsed});

            // Start timer for pre page
            this.postPageDurationStart = new Date();

            var self = this;

            // Get text from textarea
            var text = $('textarea#measurement_measurement-input-textarea').val();

            // Remove line breaks from text
            text = text.replace(/(\r\n|\n|\r)/gm," ");

            // Set text to analyzer
            this.analyzer.set({'text': text});

            // Render loading ring
            this.$el.html(this.loading_template());

            // Get text data
            this.analyzer.save(null, {
                success: function(response) {
                    self.renderSVG();
                },
                error: function(model, response) {
                    console.log(response.responseText);
                }
            });
        },

        // toggleText: function() {

        //     // Get dom elements
        //     var container = $('#measurement_measurement-review-text');
        //     var button = $('#measurement_measurement-review-toggle-button');

        //     // Textarea not visible
        //     if (!this.toggle_text) {

        //         var current_height = container.height();

        //         button.text("Bearbeitung schlie√üen");

        //         this.toggle_text = true;
        //         container.html("<textarea>" + this.text_complete_model.get('post_text') + "</textarea>");
        //         container.find('textarea').height(current_height);
        //     // Textarea visible
        //     } else {
        //         button.text("Bearbeiten");
        //         container.html("<p>" + this.text_complete_model.get('pre_text') + "</p>");
        //         this.toggle_text = false;
        //         this.setContainerHeight();
        //     }
        // },

        sendData: function() {

            // Set duration for review process
            var elapsed = (new Date() - this.postPageDurationStart) / 1000;
            this.text_complete_model.set({'post_page_duration': elapsed});

        },

        renderSVG: function() {

            // Save pre_text data
            this.text_complete_model.set({'pre_text': this.analyzer.get('text'),
                'pre_num_sentences': this.analyzer.get('numSentences'),
                'pre_num_clusters': this.analyzer.get('numClusters'),
                'pre_num_coherent_sentences': this.analyzer.get('cohSentences'),
                'pre_num_non_coherent_sentences': this.analyzer.get('cohNotSentences'),
                'pre_num_concepts': this.analyzer.get('numConcepts'),
                'post_text': this.analyzer.get('text')});

            // Render review template
            this.$el.html(this.review_template({'text': this.analyzer.get('text')}));

            // Hide Review div
            var review_div = $('#measurement_review-div');
            review_div.hide();

            // Show review div after 300 seconds
            window.setTimeout(function() {
                review_div.show(1000, 'swing');
            }, 60000);

            // Set height of container
            this.setContainerHeight();
            
            // Change padding
            this.$el.css('padding-top', "10px");
            this.$el.css('padding-left', '15px');

            // Set width and height of svg
            var current_height = $(window).height();
            var svgHeight = current_height * 0.95;
            var svgWidth = $('#measurement_measurement-graph').width();

            // Render SVG
            renderCmap(this.analyzer.get('word_pairs'), this.analyzer.get('clusters'),
                this.analyzer.get('numClusters'), '#measurement_measurement-graph',
                svgHeight, svgWidth);

        },

        setContainerHeight: function() {

            var textarea_view = $('#measurement_measurement-review-text').find('textarea');
            var current_height = $(window).height();
            textarea_view.height(current_height * 0.83);
        },

        analyzePostText: function() {
            
            var self = this;

            // Clear analyzer model
            this.analyzer.clear();

            // Set text to analyzer
            this.analyzer.clear();
            this.analyzer.set({'text': this.text_complete_model.get('post_text')});

            // Close modal
            $('#measurement_modal').foundation('close');

            // // Render loading ring
            this.$el.html(this.loading_template());

            // Change padding
            this.$el.css('padding-top', "60px");

            // Get text data
            this.analyzer.save(null, {
                success: function(response) {
                    self.saveDataMeasurement();
                },
                error: function(model, response) {
                    console.log("error");
                }
            });
        },

        saveDataMeasurement: function() {

            // Save post text data
            this.text_complete_model.set({'post_num_sentences': this.analyzer.get('numSentences'),
                'post_num_clusters': this.analyzer.get('numClusters'),
                'post_num_coherent_sentences': this.analyzer.get('cohSentences'),
                'post_num_non_coherent_sentences': this.analyzer.get('cohNotSentences'),
                'post_num_concepts': this.analyzer.get('numConcepts')});

            // Save data to server
            this.text_complete_model.save(null, {
                success: function(response) {
                    // Reload page
                    location.reload();
                },
                error: function(model, response) {
                    this.$el.html(text_save_error_template());
                }
            });
        }
    });

    // ===================== ModalView =================================
    var ModalView = Backbone.View.extend({

        el: $('#measurement_modal'),

        events: {
            "click #measurement_send-button": "sendPostText",
        },

        initialize: function() {
        },

        sendPostText: function() {
            myview.analyzePostText();
        }
    });

    // ================== Initialize ModalView ==========================
    var myview = new MeasurementView();
    var modalView = new ModalView();

})($);
//# sourceMappingURL=data:application/json;base64,
